import classNames from 'classnames';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import DomHandler from '../../utils/DomHandler';


/**
 * Component to render individual items in a list box
 */
export class ListBoxItem extends Component {
    constructor() {
        super();
        this.onClick = this.onClick.bind(this);
        this.onTouchEnd = this.onTouchEnd.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
    }

    onClick(event) {
        if (this.props.onClick) {
            this.props.onClick({
                originalEvent: event,
                option: this.props.option,
            });
        }

        event.preventDefault();
    }

    onTouchEnd(event) {
        if (this.props.onTouchEnd) {
            this.props.onTouchEnd({
                originalEvent: event,
                option: this.props.option,
            });
        }
    }

    onKeyDown(event) {
        let item = event.currentTarget;

        switch (event.which) {
            //down
            case 40:
                var nextItem = this.findNextItem(item);
                if (nextItem) {
                    nextItem.focus();
                }

                event.preventDefault();
                break;

            //up
            case 38:
                var prevItem = this.findPrevItem(item);
                if (prevItem) {
                    prevItem.focus();
                }

                event.preventDefault();
                break;

            //enter
            case 13:
                this.onClick(event);
                event.preventDefault();
                break;

            default:
                break;
        }
    }

    findNextItem(item) {
        let nextItem = item.nextElementSibling;

        if (nextItem)
            return DomHandler.hasClass(nextItem, 'p-disabled')
                ? this.findNextItem(nextItem)
                : nextItem;
        else return null;
    }

    findPrevItem(item) {
        let prevItem = item.previousElementSibling;

        if (prevItem)
            return DomHandler.hasClass(prevItem, 'p-disabled')
                ? this.findPrevItem(prevItem)
                : prevItem;
        else return null;
    }

    render() {
        let className = classNames('p-listbox-item', {
            'p-highlight': this.props.selected,
        });
        let content = this.props.template
            ? this.props.template(this.props.option)
            : this.props.label;

        return (
            <li
                className={className}
                onClick={this.onClick}
                onTouchEnd={this.onTouchEnd}
                onKeyDown={this.onKeyDown}
                tabIndex={this.props.tabIndex}
            >
                {content}
            </li>
        );
    }
}

ListBoxItem.defaultProps = {
    option: null,
    label: null,
    selected: false,
    tabIndex: null,
    onClick: null,
    onTouchEnd: null,
    template: null,
};

ListBoxItem.propTypes = {
    /**
     * Listbox option
     */
    option: PropTypes.any,
    /**
     * Label to display
     */
    label: PropTypes.string,
    /**
     * Boolean. If the option has been selected or not
     */
    selected: PropTypes.bool,
    /**
     * Index of the option
     */
    tabIndex: PropTypes.string,
    /**
     * Callback to invoke when the option is clicked.
     */
    onClick: PropTypes.func,
    /**
     * Callback to invoke when the option is clicked.
     */
    onTouchEnd: PropTypes.func,
    /**
     * Template funtion to use
     */
    template: PropTypes.func,
};
